import {Link} from 'react-router-dom';
import '../assets/css/pages/login.css';


// Images
import IcGoogle from "../assets/images/icons/ic-google.svg"
import IcFbLg from "../assets/images/icons/ic-fb-lg.svg"
import IcApple from "../assets/images/icons/ic-apple.svg"

function Login() {
    return (
        <section className="auth-form padding-60">
            <div className="container">
                <form>
                    <div className="form-box">
                        <div className="section-title varient-two">
                            <h4>Login</h4>
                        </div>
                        <div className="form-group">
                            <label htmlFor="email" className="form-label">Email</label>
                            <input type="email" className="form-control" id="email" placeholder="Enter your email" />
                        </div>
                        <div className="form-group">
                            <label htmlFor="password" className="form-label">Password</label>
                            <input type="password" className="form-control" id="password" placeholder="Enter your password" />
                        </div>
                        <div className="text-end">
                            <Link to="/forgotPassword" className="f-ps-link">Forgot Password?</Link>
                        </div>
                        <div className="row form-submit-btn">
                            <div className="col-lg-6">
                                <button className="button button-blue w-100" type="submit">Login</button>
                            </div>
                        </div>
                        <ul className="social-login">
                            <li>
                                <Link to="/" className="google"><img src={IcGoogle} alt="" /></Link>
                            </li>
                            <li>
                                <Link to="/" className="fb"><img src={IcFbLg} alt="" /></Link>
                            </li>
                            <li>
                                <Link to="/" className="apple"><img src={IcApple} alt="" /></Link>
                            </li>
                        </ul>
                        <p className="goto">Donâ€™t have an account? <Link to="/signup">Sign up</Link></p>
                    </div>
                </form>
            </div>
        </section>
    )
}

export default Login;